// Подключение шрифтов ===========================================================================================================================================================================================================================================================================
//&display=swap - добавить при подключении через плагин
@import url(https://fonts.googleapis.com/css?family=Roboto:regular,700,900&display=swap);
// Подключить если есть локальные файлы шрифтов
//@import "fonts/fonts";

// Подключение иконочных шрифтов ==============================================================================================================================================================================================================================================================================
// Подключить если есть файл иконочного шрифта
//@import "fonts/icons";

// Шрифт по умолчанию ==============================================================================================================================================================================================================================================================================
$fontFamily: "Roboto";
$fontSize: 14px; // где 14(px) - размер шрифта по умолчанию из макета
$lineHeight: 17px;


// Основные цвета
$mainColor: #000; // Цвет шрифта по умолчанию
$orangeColor: #FE5F1E;


// НАСТРОЙКИ ===============================================================================================================================================================================================================================================================================================
// Минимальная ширина страницы
$minWidth: 320px;
// Ширина полотна (макета)
$maxWidth: 1440px;
// Ширина ограничивающего контейнера (0 = нет ограничения)
$maxWidthContainer: 1225px;
// Общий отступ у контейнера
// (15px слева и справа, 0 = нет отступа)
$containerPadding: 30px;

// Ширина срабатывания первого брейкпоинта
$containerWidth: $maxWidthContainer + $containerPadding;

// Брейк-поинты
$pc: $containerWidth; // ПК, ноутбуки, некоторые планшеты в горизонтальном положении
$tablet: 991.98px; // Планшеты, некоторые телефоны в горизонтальном положении
$mobile: 767.98px; // Телефоны L
$mobileSmall: 479.98px; // Телефоны S


// Обнуление и общие параметры ============================================================================================================================================================================================== =============================================================================================== ===============================================================================================
@import "./scss/null.scss";

// Оболочка 
.wrapper {
  min-height: 100%;
  // display: flex;
  // flex-direction: column;
  display: grid;
  grid-template-rows: auto 1fr;
  background: #FFFFFF;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.03);

  // Прижимаем footer
  .main {
    // flex: 1 1 auto;
  }

  // Фикс для слайдеров
  >* {
    min-width: 0;
  }
}

// Ограничивающий контейнер ======================================================================================================================================================================================================================

/*
(i) Стили будут применяться ко 
всем классам содержащим *__container
Например header__container, main__container и т.д.
Снипет (HTML): cnt
*/

// Отзывчивая
[class*="__container"] {
  @if ($maxWidthContainer>0) {
    max-width: $maxWidthContainer;
    box-sizing: content-box;
    margin: 0 auto;
  }

  @if ($containerPadding>0) {
    padding: 0 $containerPadding;
  }
}

