* {
   padding: 0px;
   margin: 0px;
   border: 0px;
}

*,
*:before,
*:after {
   box-sizing: border-box;
}

html,
body {
   height: 100%;
   min-width: $minWidth + px;
}

body {
   color: $mainColor;
   line-height: 1;
   font-family: $fontFamily;
   font-size: $fontSize;
   line-height: $lineHeight;
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   display: flex;
   flex-direction: column;
   padding: 40px;
   background: #FFDF8C;

   @media (max-width:$mobile) {
      padding: 5px;
   }
}

#root {
   min-height: 100%;
   display: flex;
   flex-direction: column;
}

input,
button,
textarea {
   font-family: $fontFamily;
   font-size: inherit;
}

button {
   cursor: pointer;
   color: inherit;
   background-color: inherit;
}

a {
   color: inherit;
}

a:link,
a:visited {
   text-decoration: none;
}

a:hover {
   text-decoration: none;
}

ul li {
   list-style: none;
}

img {
   vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   font-weight: inherit;
   font-size: inherit;
}