.header {
   padding: 49px 0px 40px;
   background: #fff;
   // .header__container

   &__container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 5px;
   }

   // .header__picture

   &__picture {}

   // .header__link

   &__link {}

   // .header__logo

   &__logo {
      max-width: 100%;
   }

   // .header__button

   &__button {
      position: fixed;
      top: 80px;
      right: 70px;
      display: flex;
      align-items: center;
      gap: 60px;
      background: $orangeColor;
      padding: 15px 23px 16px;
      border-radius: 30px;
      color: #fff;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      cursor: pointer;
      transition: all 0.3s ease;
      white-space: nowrap;
      z-index: 10;

      &::before {
         content: '';
         position: absolute;
         width: 1px;
         height: 25px;
         background: rgba(255, 255, 255, 0.25);
         left: 50%;
      }

      &:hover {
         background: rgb(202, 70, 8);
      }

      @media (max-width:$mobile) {
         top: 10px;
         right: 10px;
         padding: 10px 15px 10px;
         gap: 45px;
         opacity: 0.5;

         &.header__button::before {
            left: 45%;
         }
      }
   }

   // .header__price

   &__price {}

   // .header__number

   &__number {
      position: relative;

      &::before {
         content: '';
         position: absolute;
         width: 18px;
         height: 18px;
         background: url('../../img/header/iconbasket.svg') no-repeat;
         left: -26px;
      }
   }
}