.page {
   display: flex;
   flex-direction: column;
}

.basket {
   background: #fff;
   display: flex;
   flex: 1 1 auto;
   padding: 50px 0px;
   // .basket__container

   &__container {
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;

      &_center {
         align-items: center;
      }
   }

   // .basket__title

   &__title {
      font-weight: 700;
      font-size: 32px;
      line-height: 39px;
      letter-spacing: 0.01em;

      @media (max-width:$mobileSmall) {
         font-size: 25px;
         line-height: 29px;
         text-align: center;
      }
   }

   // .basket__text

   &__text {
      margin-top: 15px;
      font-size: 18px;
      line-height: 145.4%;
      text-align: center;
      letter-spacing: 0.01em;
      color: #777777;
      max-width: 547px;
      margin-bottom: 47px;
   }

   // .basket__picture

   &__picture {
      margin-bottom: 50px;
   }

   // .basket__img

   &__img {
      max-width: 100%;
   }

   // .basket__link

   &__link {
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      letter-spacing: 0.015em;
      padding: 15px 25px;
      background: #282828;
      color: #fff;
      border-radius: 30px;
      display: inline-block;
      transition: all 0.3s ease;

      &:hover {
         background: #000000;
      }
   }

   // .basket__box

   &__box {
      display: flex;
      justify-content: space-between;
      gap: 30px;
      align-items: center;
      margin-bottom: 40px;
   }

   // .basket__clear

   &__clear {
      color: #B6B6B6;
      transition: all 0.3s ease;

      &:hover {
         color: #282828;
      }
   }

   // .basket__body

   &__body {
      margin-bottom: 40px;
      flex: 1 1 auto;
   }

   // .basket__items

   &__items {
      &:not(:last-child) {
         margin-bottom: 40px;
      }

   }

   // .basket__details

   &__details {
      margin-bottom: 40px;
   }

   // .basket__block

   &__block {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 30px;

      @media (max-width:$mobileSmall) {
         flex-wrap: wrap-reverse;
         gap: 10px;
         justify-content: center;
      }
   }

   // .basket__button

   &__button {
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      letter-spacing: 0.015em;
      padding: 15px 25px;
      background: #EB5A1E;
      color: #fff;
      border-radius: 30px;
      display: inline-block;
      transition: all 0.3s ease;

      &:hover {
         background: #ce4a11;
      }
   }
}

.pizza-block {
   display: flex;
   align-items: center;
   justify-content: space-between;
   gap: 30px;

   @media (max-width:$mobileSmall) {
      flex-wrap: wrap;
      gap: 20px;
      justify-content: space-around;
   }

   // .pizza-block__box

   &__box {
      display: flex;
      align-items: center;
      gap: 30px;

      @media (max-width:$mobile) {
         gap: 10px;
         flex-wrap: wrap;
         justify-content: flex-start;
      }

      @media (max-width:$mobileSmall) {
         justify-content: center;
         text-align: center;
      }

   }

   // .pizza-block__picture

   &__picture {
      max-width: 100px;
   }

   // .pizza-block__img

   &__img {
      max-width: 100%;
   }

   // .pizza-block__paragraph

   &__paragraph {
      display: flex;
      flex-direction: column;
      gap: 3px;
      min-width: 173px;
   }

   // .pizza-block__name

   &__name {
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;
      letter-spacing: 0.01em;

      @media (max-width:$mobile) {
         font-size: 16px;
         line-height: 18px;
      }
   }

   // .pizza-block__param

   &__param {
      font-size: 18px;
      line-height: 22px;
      letter-spacing: 0.01em;
      color: #8D8D8D;

      @media (max-width:$mobile) {
         font-size: 16px;
         line-height: 18px;
      }
   }

   // .pizza-block__price

   &__price {
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;
      letter-spacing: 0.01em;
      white-space: nowrap;
   }

   // .pizza-block__delete

   &__delete {
      color: #D0D0D0;
      font-weight: 700;
      transition: all 0.3s ease;

      &:hover {
         color: #000000;
      }
   }
}

.details {
   display: flex;
   justify-content: space-between;
   align-items: center;
   gap: 30px;
   font-size: 22px;
   line-height: 27px;
   letter-spacing: 0.01em;
   white-space: nowrap;

   @media (max-width:$mobileSmall) {
      flex-wrap: wrap;
      gap: 10px;
      justify-content: center;
   }

   // .details__number

   &__number {}

   // .details__counter

   &__counter {
      font-weight: 700;
   }

   // .details__price

   &__price {

      // .details__price_color

      &_color {
         font-weight: 700;
         color: #EB5A1E;
      }
   }
}