.page {}

.home {
   background: #fff;
   padding: 50px 0 100px;
   // .home__container

   &__container {}

   // .home__navigation

   &__navigation {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
      margin-bottom: 32px;

      @media (max-width:$pc) {
         align-items: flex-start;
      }
   }

   // .home__menu

   &__menu {}

   // .home__select

   &__select {
      display: flex;
      align-content: flex-start;
      justify-content: flex-end;
      gap: 5px;
      position: relative;
      flex-wrap: wrap;
   }

   // .home__text

   &__text {
      white-space: nowrap;
   }

   // .home__form

   &__form {
      cursor: pointer;
      color: $orangeColor;
      text-decoration: underline;
   }

   // .home__pupap

   &__pupap {
      background: #FFFFFF;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
      border-radius: 10px;
      padding: 20px 15px;
      position: absolute;
      top: 30px;
      right: 0;
      display: flex;
      flex-direction: column;
      gap: 15px;
      opacity: 0;
      animation: visiblePupap 0.5s ease 0s forwards;
   }

   // .home__variant

   &__variant {
      cursor: pointer;

      &:hover {
         text-decoration: underline;
         color: $orangeColor;
      }
   }

   // .home__title

   &__title {
      font-weight: 700;
      font-size: 32px;
      line-height: 39px;
      letter-spacing: 0.01em;
      margin-bottom: 35px;
   }

   // .home__items

   &__items {
      display: grid;
      grid-template-columns: repeat(4, minmax(auto, 280px));
      gap: 20px;

      @media (max-width:$pc) {
         grid-template-columns: repeat(3, minmax(auto, 280px));
         justify-content: center;
      }

      @media (max-width:$tablet) {
         grid-template-columns: repeat(2, minmax(auto, 280px));
         justify-content: center;
      }

      @media (max-width:$mobile) {
         grid-template-columns: repeat(1, minmax(auto, 280px));
         justify-content: center;
         gap: 40px;
      }
   }

   // .home__item

   &__item {}
}

.menu {

   // .menu__body

   &__body {}

   // .menu__list

   &__list {
      display: flex;
      gap: 30px;
      flex-wrap: wrap;

      @media (max-width:$tablet) {
         gap: 10px;
      }
   }

   // .menu__item

   &__item {
      background: #F9F9F9;
      padding: 15px 10px;
      border-radius: 30px;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      letter-spacing: 0.015em;
      cursor: pointer;

      @media (max-width:$mobileSmall) {
         padding: 5px;
         font-weight: 700;
         font-size: 14px;
      }
   }

}

.pupap-active {
   font-weight: 700;
   color: $orangeColor;
}

@keyframes visiblePupap {
   0% {
      opacity: 0;
   }

   100% {
      opacity: 1;
   }
}

.active-menu {
   transition: all 0.5s ease;
   background: #282828;
   color: #fff;
}

.item-pizza {
   display: flex;
   flex-direction: column;
   // .item-pizza__picture

   &__picture {
      margin-bottom: 11px;
      text-align: center;
   }

   // .item-pizza__img

   &__img {
      max-width: 80%;
   }

   // .item-pizza__title

   &__title {
      font-weight: 800;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
      letter-spacing: 0.01em;
      margin-bottom: 22px;
      flex: 1 1 auto;
   }

   // .item-pizza__body

   &__body {
      background: #F3F3F3;
      border-radius: 10px;
      font-weight: 700;
      text-align: center;
      letter-spacing: 0.015em;
      padding: 7px 5px;
      display: flex;
      flex-direction: column;
      gap: 5px;
      margin-bottom: 17px;
   }

   // .item-pizza__categories

   &__categories {
      display: flex;
      gap: 6px;
   }

   // .item-pizza__category

   &__category {
      padding: 8px 25px;
      flex: 0 1 50%;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
      border-radius: 5px;
      cursor: pointer;
   }

   // .item-pizza__sizes

   &__sizes {
      display: flex;
      gap: 6px;
   }

   // .item-pizza__size

   &__size {
      flex: 0 1 33.333%;
      padding: 8px;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
      border-radius: 5px;
      cursor: pointer;
   }

   // .item-pizza__block

   &__block {
      display: flex;
      align-items: center;
      gap: 6px;
      justify-content: space-around;
   }

   // .item-pizza__price

   &__price {
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;
      letter-spacing: 0.015em;
   }

   // .item-pizza__button

   &__button {
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      color: #EB5A1E;
      background: #FFFFFF;
      border: 1px solid #EB5A1E;
      border-radius: 30px;
      padding: 11px 20px 14px 35px;
      display: inline-block;
      position: relative;
      transition: all 0.3s ease;

      &:before {
         content: '';
         width: 3px;
         height: 14px;
         background: #EB5A1E;
         position: absolute;
         top: 50%;
         left: 20px;
         transform: translateY(-50%);
         border-radius: 30px;
      }

      &::after {
         content: '';
         width: 14px;
         height: 3px;
         background: #EB5A1E;
         position: absolute;
         top: 50%;
         left: 14px;
         transform: translateY(-50%);
         border-radius: 30px;

      }

      &:hover {
         color: #fff;
         background: #EB5A1E;

         &:before,
         &::after {
            background: #fff;
         }

         .item-pizza__number {
            color: #EB5A1E;
         }
      }
   }
}

.active-body {
   background: #FFFFFF;
}

.disable-body {
   opacity: 0.5;
   pointer-events: none;
}